<template>
	<div class="singer">
		<div class="tagOne" @click="changeTag()">
			<ul style="text-align: left;">
			
				<h4><span class="item" :class="{active:languageTag==-1&&genderTag==-1}" @click="genderTag=-1,languageTag=-1"></span></h4>
			<el-divider></el-divider>
			
			<h4></h4>
			<li><span class="item" :class="{active:languageTag==7&&genderTag==1}" @click="genderTag=1,languageTag=7"></span></li>
			<li><span class="item" :class="{active:languageTag==7&&genderTag==2}" @click="genderTag=2,languageTag=7"></span></li>
			<li><span class="item" :class="{active:languageTag==7&&genderTag==3}" @click="genderTag=3,languageTag=7">/</span></li>
			<el-divider></el-divider>
			
			<h4></h4>
			<li><span class="item" :class="{active:languageTag==96&&genderTag==1}" @click="genderTag=1,languageTag=96"></span></li>
			<li><span class="item" :class="{active:languageTag==96&&genderTag==2}" @click="genderTag=2,languageTag=96"></span></li>
			<li><span class="item" :class="{active:languageTag==96&&genderTag==3}" @click="genderTag=3,languageTag=96">/</span></li>
			<el-divider></el-divider>
			
			<h4></h4>
			<li><span class="item" :class="{active:languageTag==8&&genderTag==1}" @click="genderTag=1,languageTag=8"></span></li>
			<li><span class="item" :class="{active:languageTag==8&&genderTag==2}" @click="genderTag=2,languageTag=8"></span></li>
			<li><span class="item" :class="{active:languageTag==8&&genderTag==3}" @click="genderTag=3,languageTag=8">/</span></li>
			<el-divider></el-divider>
			
			<h4></h4>
			<li><span class="item" :class="{active:languageTag==16&&genderTag==1}" @click="genderTag=1,languageTag=16"></span></li>
			<li><span class="item" :class="{active:languageTag==16&&genderTag==2}" @click="genderTag=2,languageTag=16"></span></li>
			<li><span class="item" :class="{active:languageTag==16&&genderTag==3}" @click="genderTag=3,languageTag=16">/</span></li>
			<el-divider></el-divider>
			
			<h4></h4>
			<li><span class="item" :class="{active:languageTag==0&&genderTag==1}" @click="genderTag=1,languageTag=0"></span></li>
			<li><span class="item" :class="{active:languageTag==0&&genderTag==2}" @click="genderTag=2,languageTag=0"></span></li>
			<li><span class="item" :class="{active:languageTag==0&&genderTag==3}" @click="genderTag=3,languageTag=0">/</span></li>
			</ul>
		</div>
		
		<div class="tagTwo" @click="changeTag">
			<span class="item" v-for="(item,index) in initials"
			:key="index"
			style="padding-left: 10px;" :class="{active:initialTag==item[1]}" @click="initialTag=item[1]" >
				{{item[0]}}
			</span>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default{
		data(){
			return{
				// 
				languageTag: -1,
				genderTag: -1,
				initialTag: -1,
				
				// 
				initials: [
				  ["", -1],
				  ["A", "a"],
				  ["B", "b"],
				  ["C", "c"],
				  ["D", "d"],
				  ["E", "e"],
				  ["F", "f"],
				  ["G", "g"],
				  ["H", "h"],
				  ["I", "i"],
				  ["J", "j"],
				  ["K", "k"],
				  ["L", "l"],
				  ["M", "m"],
				  ["N", "n"],
				  ["O", "o"],
				  ["P", "p"],
				  ["Q", "q"],
				  ["R", "r"],
				  ["S", "s"],
				  ["T", "t"],
				  ["U", "u"],
				  ["V", "v"],
				  ["W", "w"],
				  ["X", "x"],
				  ["Y", "y"],
				  ["Z", "z"],
				  ["#", 0],
				]
			}
		},
		created() {
			this.changeTag()
		},
	// created() {
	// 	axios({
	// 		url:'https://autumnfish.cn/artist/list',
	// 		method:'GET',
	// 		params:{
	// 			type:"-1",
	// 			area:"-1",
	// 			initial:"a"
	// 		}
			
	// 	}).then(res=>{
	// 		console.log(res)
	// 	})
	// },
	methods:{
		changeTag(){
			this.$emit("getSingerInfo",this.genderTag,this.languageTag,this.initialTag)
		}
	}
	
		
	}
</script>

<style>
	.singer{
		width: 100%;
		height: 100%;
	}
	.tagOne{
		float: left;
		width: 200px;
		height: 950px;
		background-color: rgb(249,249,249);
		border-left:1px solid rgb(236,236,236) ;
		border-right:1px solid rgb(236,236,236) ;
	}
	li{
		line-height: 25px;
	}
	.tagTwo{
		width: 1010px;
		height: 50px;
		float: left;
	}
	.item{
		cursor:pointer;
		
	}
	.active{
		color: firebrick;
		border: 1px solid rgb(236,236,236);
	}
</style>



