<template>
	<div class="playLists">
		
		
		<!--                -->
		<div class="tag" @click="tag()" >
			<span class="item" style="float: left;" :class="{active:actTag==''}" @click="actTag=''"></span>
			<div class="tagHead" v-for="(item,index) in alltags" :key="index">
				<span class="item" :class="{active:actTag==item.name}" @click="actTag=item.name">{{item.name}}</span>
			</div>
			<!-- <h1>{{actTag}}</h1> -->
		</div>
		<h3></h3>
		
		
		<!--        prop -->
		<playList :allList="allList" v-on:getPLId='toPlaylistDetail'></playList>
		
		
		<!--    currentchange    -->
		<div class="pagination">
				 <el-pagination
					@current-change="handleCurrentChange"
					background
					layout="prev,pager,next"
					:total="total"
					:current-page="page"
					:page-size="36"></el-pagination>
		</div>
	
		
	</div>
</template>

<script>
	import axios from 'axios'
	import playList from '../../components/PlayLists/PlayList.vue'
	export default{
		components:{
			playList
		},
		data(){
			return {
				alltags:[],
				//
				actTag:"",
				allList:[],
				//
				total:0,
				//
				page:1
				}
			},
		methods:{
			//    tag   acttag 
			tag(){
				// page
				this.page=1
				// console.log(this.actTag)
				axios({
				url:'/top/playlist/',
				method:'GET',
				params:{
					limit:36,
					
					//  (-1)*
					offset:(this.page-1)*36,
					//
					cat:this.actTag
					
				} 
			}).then(res=>{
				// console.log(res)
				
				this.allList=res.data.playlists
				this.total=res.data.total
				
			})
			 },
			//
				tags(){
					// console.log(this.actTag)
					axios({
					url:'/top/playlist/',
					method:'GET',
					params:{
						limit:36,
						
						//  (-1)*
						offset:(this.page-1)*36,
						//
						cat:this.actTag
						
					} 
				}).then(res=>{
					// console.log(res)
					
					this.allList=res.data.playlists
					this.total=res.data.total
					
				})
				 },
				
				//
				handleCurrentChange(val){
					// console.log(`${val}`)
					this.page=val
					this.tags()
				},
				
				toPlaylistDetail(id){
					//
					this.$router.push(`/playlistDetail?q=${id}`)
				},
				
			},
		created() {
				//
				axios({
					url:'/playlist/hot',
					method:'GET',
					
				}).then(res=>{
					
					this.alltags=res.data.tags
				}),
				
				//
				this.tags()
			}
			
			
		
	}
</script>

<style>
	@import url("../../assets/css/all.css");
	.playLists{
		
			width: 1220px;
			height: 100%;
			color: black;
			margin: 0 auto;
		
	}
	.tag{
			line-height: 8px;	
			 /* border-bottom: 2px solid firebrick ; */
		}
	.tagHead{
		float: left;
	
	}
	span{
		margin-right: 10px;
		cursor: pointer;
	}
	.active{
		color: firebrick;
		
	}
	.pagination{
		
		margin-bottom:  60px;
	
			
	}



</style>



